import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {ApiProvider} from "../api/api";
import {File} from "./file.modal";
import {ElectronProvider} from "../electron/electron";
import {isCordovaAvailable} from "../../services/is-cordova-available";
import {StorageManagerProvider} from "../storage-manager/storage-manager";
import {GuiManagerProvider} from "../gui-manager/gui-manager";
import {ResponseContentType} from "@angular/http";
import {File as FileManager} from '@ionic-native/file';
import {FileOpener} from "@ionic-native/file-opener";
import {OtpProvider} from "../otp/otp";
import {AlertController} from "ionic-angular";
import {PlatformManagerProvider} from "../platform-manager/platform-manager";

/*
  Generated class for the FileManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FileManagerProvider {

  public cached: File;

  public fileIds = [];
  private fetching = false;

  private key: string;
  private isMedical: boolean;
  private name: string;
  private encodedEmailId: string;

  constructor(public http: ApiProvider, private electron: ElectronProvider, private storage: StorageManagerProvider, private guiManager: GuiManagerProvider, private fileManager: FileManager, private opener: FileOpener, private otp: OtpProvider, private alertCtrl: AlertController, private platformUtils: PlatformManagerProvider) {

    this.encodedEmailId = "";

  }

  public clear() {
    this.fileIds = [];
    this.cached = new File();
    this.key = "";
    this.isMedical = false;
    this.name = "";
  }

  private writeDataToFileAndOpen(data, file: File, notUseOTP: boolean) {
    const me = this;
    const id = file.fileId;
    const hash = file.hashcode;
    const strid: string = file.fileId + "";

    if (notUseOTP) {
      if (me.platformUtils.isWebApplication()) {
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(data.blob());
        //link.download = item.name;
        link.download = "Aufnahmefragebogen.pdf";
        link.click();
      }
      else {
        const path = me.fileManager.dataDirectory.substr(7) + "/" + strid + "/" + file.name;

        me.fileManager.checkDir(this.fileManager.dataDirectory, strid).then(exists => {
          console.log("The folder does exist");
          me.fileManager.writeFile(this.fileManager.dataDirectory + "/" + strid, file.name, data.blob(), {replace: true}).then(written => {
            console.log("saved ", file.name , " file");
            console.log("Path is: "+ me.fileManager.dataDirectory.substr(7)  + strid + "/" + file.name);
            me.storage.save("file-" + id, {id: id, hashcode: hash}, () => {
              me.openFile(file);
            });
          }).catch(err => {
          })
        }).catch(err => {

          console.log("Folder does not exist")
          me.fileManager.createDir(this.fileManager.dataDirectory, strid, true).then(_ => {

            console.log("folder created")
            me.fileManager.writeFile(this.fileManager.dataDirectory + "/" + strid, file.name, data.blob(), {replace: true}).then(written => {
              console.log("saved ", file.name , " file");
              console.log("Path is: "+ me.fileManager.dataDirectory.substr(7)  + strid + "/" + file.name);
              me.storage.save("file-" + id, {id: id, hashcode: hash}, () => {
                me.openFile(file);
              });
            }).catch(err => {
            })
          });
        })
      }
    }
    else {

      me.fileManager.checkDir(this.fileManager.dataDirectory, strid).then(exists => {
        console.log("The folder does exist");
        me.fileManager.writeFile(this.fileManager.dataDirectory + "/" + strid, file.name + ".crypt", data.blob(), {replace: true}).then(written => {
          console.log("wrote .crypt file");
          console.log("Path is: "+ me.fileManager.dataDirectory.substr(7)  + strid + "/" + file.name)

          console.log(me.fileManager.dataDirectory.substr(7) + "key.otp");
          console.log(me.fileManager.dataDirectory.substr(7) + strid + "/" + file.name + ".crypt")
          me.otp.resetKey();
          me.otp.decrypt(me.fileManager.dataDirectory.substr(7) + "/key.otp", me.fileManager.dataDirectory.substr(7) + "/" + strid + "/" + file.name + ".crypt", me.fileManager.dataDirectory.substr(7) + "/" + strid + "/" + file.name, (success) => {


            console.log(success)
            me.storage.save("file-" + id, {id: id, hashcode: hash}, () => {
              me.openFile(file);

            });
          }, (error) => {
            console.log(error)
          })
        }).catch(err => {
        })
      }).catch(err => {

        console.log("Folder does not exist")
        me.fileManager.createDir(this.fileManager.dataDirectory, strid, true).then(_ => {

          console.log("folder created")
          me.fileManager.writeFile(this.fileManager.dataDirectory + "/" + strid, file.name + ".crypt", data.blob(), {replace: true}).then(written => {
            console.log("wrote .crypt file");
            console.log("Path is: "+ me.fileManager.dataDirectory.substr(7)  + strid + "/" + file.name)
            me.otp.resetKey();
            me.otp.decrypt(me.fileManager.dataDirectory.substr(7) + "/key.otp", me.fileManager.dataDirectory.substr(7) + "/" + strid + "/" + file.name + ".crypt", me.fileManager.dataDirectory.substr(7) + "/" + strid + "/" + file.name, (success) => {
              console.log(success)
              me.openFile(file);
            }, (error) => {
              console.log(error)

            })


          }).catch(err => {
          })
        });
      })
    }
  }

  private downloadFileWithHTTPS(item: File) {

      const me = this;
      const id = item.fileId;
      const hash = item.hashcode;

      const cont = () => {
          const strid: string = item.fileId + "";

          let data = null;


          if (item.rawLink == null || item.rawLink == "") {
              data = encodeURIComponent(btoa(JSON.stringify({fid: id, key: this.key, crypt: false})));
          } else {
              data = item.rawLink;
          }
          {
              if (!isCordovaAvailable()) {
                  window.open(this.http.target + "/download/" + data, '_blank');
              } else {
                  if (this.electron.isElectron) {

                      me.electron.downloadFile(item.name, data, (data) => {
                          me.guiManager.showToast("Datei wurde geladen", 6500);
                      })

                  } else {
                      const indicator = this.guiManager.showToast("Die Datei wird geladen", 60000);
                      this.http.get("/download/" + data, [], (success, data) => {
                          indicator.dismissAll();
                        this.writeDataToFileAndOpen(data, item, true)

                      }, false, ResponseContentType.Blob);
                  }
              }
          }
      };

      this.storage.contains(this.encodedEmailId + "file-" + id, (contains) => {

          if (contains) {
              me.storage.fetch(this.encodedEmailId + "file-" + id, (obj) => {
                  if (obj.hashcode == hash) {
                      this.openFile(item);
                  } else {
                      cont();
                  }
              })
          } else {
              cont();
          }
      });
  }

  public downloadFile(item: File) {

    const abort = () => {

      let alerter = this.alertCtrl.create({
        title: "Download im Browser nicht mglich",
        message: "Das herunterladen von Dateien ist nicht vom Browser mglich, bitte benutzen sie die mobile App, oder die Destop Software.",
        buttons: [{text: "Ok", handler: () => {}}]
      }).present();

    };

    if(!isCordovaAvailable()) {

      abort();
      return;
    }

    if(this.otp.shouldNotRunOTP()) {
      //abort();
      this.downloadFileWithHTTPS(item);
      return;
    }

    this.otp.checkOtpTestFile();
    const me = this;
    const id = item.fileId;
    const hash = item.hashcode;

    const cont = () => {
      const strid: string = item.fileId + "";

      let data = null;


      if (item.rawLink == null || item.rawLink == "") {
        data = encodeURIComponent(btoa(JSON.stringify({fid: id, key: this.key, crypt: true})));
      } else {
        data = item.rawLink;
      }
      {
        if (!isCordovaAvailable()) {
          window.open(this.http.target + "/download/" + data, '_blank');
        } else {
          if (this.electron.isElectron) {

            me.electron.downloadFile(item.name, data, (data) => {
              me.guiManager.showToast("Datei wurde geladen", 6500);
            })

          } else {
            const indicator = this.guiManager.showToast("Die Datei wird geladen", 60000);
            this.http.get("/download/" + data, [], (success, data) => {
              indicator.dismissAll();
              this.writeDataToFileAndOpen(data, item, false)

            }, false, ResponseContentType.Blob);
          }
        }
      }
    };

    this.storage.contains("file-" + id, (contains) => {

      if (contains) {
        me.storage.fetch("file-" + id, (obj) => {
          if (obj.hashcode == hash) {
            this.openFile(item);
          } else {
            cont();
          }
        })
      } else {
        cont();
      }
    });
  }

  private openFile(item: File) {

    const strid: string = item.fileId + "";
    const id = item.fileId;
    let data = null;
    if (item.rawLink == null || item.rawLink == "") {
      data = encodeURIComponent(btoa(JSON.stringify({fid: id, key: this.key, crypt: this.electron.isElectron})));
    } else {
      data = item.rawLink;
    }
    if (!isCordovaAvailable()) {
      window.open(this.http.target + "/download/" + data, '_blank');
    } else {
      if (this.electron.isElectron) {
        //TODO
      } else {
        const path = this.fileManager.dataDirectory + strid + "/" + item.name;
        this.opener.open(path, item.type).catch(err => {
          console.log("Err while opening: " + JSON.stringify(err))
        })
      }
    }
  }

  get current() {
    return this.cached;
  }


  public init(key, isMedical, name, encodedEmailId) {
    this.key = key;
    this.isMedical = isMedical;
    this.name = name;
    this.encodedEmailId = encodedEmailId;

    const me = this;
    // this.storage.contains(this.encodedEmailId + "saved-file-ids", (cont) => {
    //   if (cont) {
    //     this.storage.fetch(this.encodedEmailId + "saved-file-ids", (data) => {
    //       me.fileIds = data;
    //       console.log("Init of file-manager. Calling updateFile");
    //       this.updateFile(() => {
    //       })
    //     })
    //   }
    //   else {
    //     console.log("Init of file-manager. Calling updateFile");
    //     this.updateFile(() => {
    //     })
    //   }
    // });
  }


  public updateFile(callback, returnNew = false) {

    const me = this;
    const newFileArr = returnNew ? [] : null;
    this.guiManager.loaders["docs"] = true;
    if (this.fetching) return;

    this.fetching = true;

    const data = {name: this.name};


    this.http.post("/search", data, [], (success, data) => {

      console.log("Returned from searching for the file manager updateFile method");
      console.log("Success, data:", success, data);
      if (success) {
        if (data.success) {

          console.log("This is success...?", data.success);

          if (data.data.level == "all") {
            const files = data.data.results;
            this.cached = new File();
            this.cached.isDirectory = true;
            this.cached.isRoot = true;
            this.cached.childrenFiles = [];
            this.cached.name = "Patienten";
            for (let i = 0; i < files.length; i++) {
              const current = files[i];
              const patientFile = new File();
              patientFile.isDirectory = true;
              patientFile.name = current.id;
              patientFile.childrenFiles = [];

              const ownFiles = current.data;
              for (let x = 0; x < ownFiles.length; x++) {
                let found = false;
                let theSub = null;
                for (let y = 0; y < patientFile.childrenFiles.length; y++) {

                  if(ownFiles[x] == null) {
                    console.log(ownFiles, x, patientFile.name)
                  }
                  if (patientFile.childrenFiles[y].name == ownFiles[x].directory) {
                    found = true;
                    theSub = patientFile.childrenFiles[y];
                    break;
                  }
                }
                if (!found) {
                  const newSubDir = new File();
                  newSubDir.parent = patientFile;
                  newSubDir.name = ownFiles[x].directory;
                  newSubDir.isDirectory = true;
                  newSubDir.childrenFiles = [];
                  patientFile.childrenFiles.push(newSubDir);
                  theSub = newSubDir;
                }
                const current = ownFiles[x];
                const obj = new File();
                obj.isDirectory = false;
                obj.name = current.name;
                obj.type = FileManagerProvider.fileTypeFetcher(current.name);
                obj.siteCount = current.count;
                obj.canOpen = current.type == "pdf" || current.type == "image";
                if (obj.canOpen) obj.links = [];
                obj.fileId = current.id;
                if (me.fileIds.indexOf(obj.fileId) == -1) {
                  me.fileIds.push(obj.fileId);
                  if (returnNew) newFileArr.push({id: obj.fileId, name: obj.name})
                }
                obj.parent = theSub;
                obj.hashcode = current.hashcode;
                /*if (current.type == "image") {
                  obj.links.push(this.generatePreviewLink(this.key, current.directory, current.name, "image"));
                } else if (current.type == "pdf") {
                  for (let c = 0; c < obj.siteCount; c++) {
                    let link = this.generatePreviewLink(this.key, current.directory, current.name, "preview", c);
                    obj.links.push(link)

                  }
                }*/
                theSub.childrenFiles.push(obj)
              }
              this.cached.childrenFiles.push(patientFile)
            }

          }
          else {
            const files = data.data;
            this.cached = new File();
            this.cached.isDirectory = true;
            this.cached.childrenFiles = [];
            this.cached.name = "Dokumente";
            this.cached.isRoot = true;
            const dirs = FileManagerProvider.defaultFolders(this.cached);
            for (let i = 0; i < files.length; i++) {
              const current = files[i];
              const obj = new File();
              obj.isDirectory = false;
              obj.name = current.name;
              obj.siteCount = current.count;
              obj.links = [];
              obj.type = FileManagerProvider.fileTypeFetcher(current.name);
              obj.canOpen = current.type == "pdf" || current.type == "image";
              /*if (current.type == "image") {
                obj.links.push(this.generatePreviewLink(this.key, current.directory, current.name, "image"));
              } else if (current.type == "pdf") {
                for (let c = 0; c < obj.siteCount; c++) {
                  let link = this.generatePreviewLink(this.key, current.directory, current.name, "preview", c);
                  obj.links.push(link)

                }
              }*/
              obj.fileId = current.id;
              if (me.fileIds.indexOf(obj.fileId) == -1) {
                me.fileIds.push(obj.fileId);
                if (returnNew) newFileArr.push({id: obj.fileId, name: obj.name})
              }
              for (let x = 0; x < dirs.length; x++) {
                if (dirs[x].name == current.directory) {
                  obj.parent = dirs[x];
                }
              }
              obj.hashcode = current.hashcode;
              if(obj.parent)
                obj.parent.childrenFiles.push(obj);
            }
            for (let x = 0; x < dirs.length; x++) {
              this.cached.childrenFiles.push(dirs[x]);
            }
          }


          me.storage.contains(this.encodedEmailId + "saved-file-ids", (contains) => {
            const next = () => {
              me.storage.save(this.encodedEmailId + "saved-file-ids", me.fileIds, () => {
                console.log("Loader should be off now.");
                me.guiManager.loaders["docs"] = false;
                if (returnNew)
                  callback(newFileArr);
                else callback();
              });
            };
            if (contains) {
              me.storage.remove(this.encodedEmailId + "saved-file-ids", () => {
                next()
              })
            } else {
              next()
            }
          })
        }
        else {
          me.guiManager.showToast("Dokumente knnen nicht vom Server geladen werden. Erneuter Versuch, Daten zu erhalten...", 5000);
          this.fetching = false;
          setTimeout( () => {
            if (this.key != "") {
              this.updateFile((newArray) => {
                if (returnNew)
                  callback(newArray);
                else callback();
              }, returnNew);
            }
          }, 5000);
          console.log("Error receiving document data from server. Re-trying to receive Loader should be off now though.")
        }
      }
      else {

          //me.guiManager.loaders["docs"] = false;
          me.guiManager.showToast("Dokumente knnen nicht vom Server geladen werden. Erneuter Versuch, Daten zu erhalten...", 5000);
          this.fetching = false;
          setTimeout( () => {
            if (this.key != "") {
              this.updateFile((newArray) => {
                if (returnNew)
                  callback(newArray);
                else callback();
              }, returnNew);
            }
          }, 5000);
          console.log("Error receiving document data from server. Re-trying to receive Loader should be off now though.")
      }

      this.fetching = false;
    }, this.key, true)


  }

  private static defaultFolders(parent) {
    const generalFiles = new File();
    const medicalFiles = new File();

    {
      generalFiles.name = "Allgemeine-Dokumente";
      generalFiles.childrenFiles = [];
      generalFiles.isDirectory = true;
      generalFiles.parent = parent;

    }
    {
      medicalFiles.name = "Medizinische-Dokumente";
      medicalFiles.childrenFiles = [];
      medicalFiles.isDirectory = true;
      medicalFiles.parent = parent;

    }

    return [generalFiles, medicalFiles];
  }

  public static fileTypeFetcher(name) {
    if (name.endsWith(".pdf")) return "application/pdf";
    if (name.endsWith(".png")) return "image/png";
    if (name.endsWith(".jpg")) return "image/jpg";
    if (name.endsWith(".doc")) return "application/msword";
    if (name.endsWith(".dot")) return "application/msword";
    if (name.endsWith(".docx")) return "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
    if (name.endsWith(".dotx")) return "application/vnd.openxmlformats-officedocument.wordprocessingml.template";
    if (name.endsWith(".docm")) return "application/vnd.ms-word.document.macroEnabled.12";
    if (name.endsWith(".dotm")) return "application/vnd.ms-word.template.macroEnabled.12";
    if (name.endsWith(".xls")) return "application/vnd.ms-excel";
    if (name.endsWith(".xlt")) return "application/vnd.ms-excel";
    if (name.endsWith(".xla")) return "application/vnd.ms-excel";
    if (name.endsWith(".xlsx")) return "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
    if (name.endsWith(".xltx")) return "application/vnd.openxmlformats-officedocument.spreadsheetml.template";
    if (name.endsWith(".xlsm")) return "application/vnd.ms-excel.sheet.macroEnabled.12";
    if (name.endsWith(".xltm")) return "application/vnd.ms-excel.template.macroEnabled.12";
    if (name.endsWith(".xlam")) return "application/vnd.ms-excel.addin.macroEnabled.12";
    if (name.endsWith(".xlsb")) return "application/vnd.ms-excel.sheet.binary.macroEnabled.12";
    if (name.endsWith(".ppt")) return "application/vnd.ms-powerpoint";
    if (name.endsWith(".pot")) return "application/vnd.ms-powerpoint";
    if (name.endsWith(".pps")) return "application/vnd.ms-powerpoint";
    if (name.endsWith(".ppa")) return "application/vnd.ms-powerpoint";
    if (name.endsWith(".pptx")) return "application/vnd.openxmlformats-officedocument.presentationml.presentation";
    if (name.endsWith(".potx")) return "application/vnd.openxmlformats-officedocument.presentationml.template";
    if (name.endsWith(".ppsx")) return "application/vnd.openxmlformats-officedocument.presentationml.slideshow";
    if (name.endsWith(".ppam")) return "application/vnd.ms-powerpoint.addin.macroEnabled.12";
    if (name.endsWith(".pptm")) return "application/vnd.ms-powerpoint.presentation.macroEnabled.12";
    if (name.endsWith(".potm")) return "application/vnd.ms-powerpoint.template.macroEnabled.12";
    if (name.endsWith(".ppsm")) return "application/vnd.ms-powerpoint.slideshow.macroEnabled.12";
    if (name.endsWith(".mdb")) return "application/vnd.ms-access";
    return "none";
  }

  public generatePreviewLink(key: string, folder: string, file: string, method: string = "preview", site = 0): string {
    let link = this.http.target + "/" + method + "/";
    const json = JSON.stringify({dir: folder, file: file, site: site, key: key});
    link += encodeURIComponent(btoa(json));

    if (key == "") {
      return null;
    }
    return link;
  }
}



