export class File {
  name: string;
  date:string;
  isDirectory: boolean;
  parent:File;
  downloaded:boolean;
  type:string;

  rawLink:string;

  isRoot:boolean = false;

  childrenFiles: File[];

  siteCount:number;
  hashcode:string;
  fileId:string;
  canOpen:boolean;
  links:any[];



}



