import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {BackgroundFetch, BackgroundFetchConfig} from "@ionic-native/background-fetch";
import {PhonegapLocalNotification} from "@ionic-native/phonegap-local-notification";
import {FileManagerProvider} from "../file-manager/file-manager";
import {Firebase} from "@ionic-native/firebase";
import {ApiProvider} from "../api/api";
import {ElectronProvider} from "../electron/electron";
import {isCordovaAvailable} from "../../services/is-cordova-available";

/*
  Generated class for the MessagesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MessagesProvider {

  private x = 0;
  private fireBaseToken = "";

  constructor(public http: ApiProvider, private fetcher: BackgroundFetch, private noitifs: PhonegapLocalNotification, private fileManager: FileManagerProvider, private firebase: Firebase, private electron:ElectronProvider) {

  }

  public sendTestNotification() {
    this.x++;

    this.noitifs.create('Test Nachricht', {
      tag: 'message' + this.x,
      body: 'Das ist eine Test Nachricht',
      icon: 'resources/icon.png'
    });

  }

  register(code) {

    if(this.electron.isElectron || !isCordovaAvailable()) return;

    const me = this;


    const next = () => {
      me.firebase.getToken()
        .then(token => {
          me.fireBaseToken = token

          me.http.get("/push/set/" + encodeURIComponent(btoa(JSON.stringify({
            key: code,
            token: me.fireBaseToken
          }))), [], (success, data) => {
          }, true);

        });
      me.firebase.onTokenRefresh().subscribe((token: string) => {
        me.fireBaseToken = token;

        me.http.get("/push/set/" + encodeURIComponent(btoa(JSON.stringify({
          key: code,
          token: me.fireBaseToken
        }))), [], (success, data) => {

        }, true);
      });
      me.firebase.onNotificationOpen().subscribe(data => {
      });
      me.firebase.hasPermission().then(jas => {
        console.log(JSON.stringify(jas));
      })
    };
    this.firebase.grantPermission().then(_ => {

      next();
    }).catch(err => {
      console.log(err);
      next()
    });


    const config: BackgroundFetchConfig = {
      stopOnTerminate: false, // Set true to cease background-fetch from operating after user "closes" the app. Defaults to true.
    };


    this.noitifs.requestPermission().then(
      (permission) => {
        if (permission === 'granted') {

          this.fetcher.configure(config)
            .then(() => {

              this.x++;
              /*this.noitifs.create('Test message', {
                tag: 'message' + this.x,
                body: 'Jetzt knnen Nachrichten verschickt werden',
                icon: 'resources/icon.png'
              });*/

              me.fileManager.updateFile((newFiles) => {

                for (let i = 0; i < newFiles.length; i++) {
                  const current = newFiles[i];

                  me.noitifs.create("Neues Dokument!", {
                    tag: 'doc-' + current.id,
                    body: "Es ist ein neues Dokument vohanden, mit dem Namen: " + current.name,
                    icon: 'resources/icon.png'
                  });
                }
              }, true);

              this.fetcher.finish();

            });

          this.fetcher.start();

        }
      }
    );
  }
}



