  public showPromptMessage(title: string, message: string, callbackOnOkayPressed = () => {}, callbackOnCancelPress = () => {}) {
      let alert = this.alertCtrl.create({
          title: title,
          subTitle: message.replace("\n","<br>"),
          buttons: [
              {
                  text: 'Besttigen',
                  handler: _ => {
                      callbackOnOkayPressed()
                  }
              },
              {
                  text: 'Abbrechen',
                  handler: _ => {
                      callbackOnCancelPress()
                  }
              }
          ]
      });
      alert.present();
  }

  public showPopupMessage(title: string, message: string, callback = () => {}, allowBackgroundDismiss: boolean = true) {
      let alert = this.alertCtrl.create({
          title: title,
          subTitle: message.replace("\n","<br>"),
          buttons: [
              {
                  text: 'Besttigen',
                  handler: _ => {
                      callback()
                  }
              }
          ],
          enableBackdropDismiss: allowBackgroundDismiss
      });
      alert.present();
  }

  public showToast(text: string, time, extraClass: string = "") {
    let instance = this.toastCtrl.create({
      message: text,
      duration: time,
      position: 'bottom',
      cssClass: extraClass
    });
    instance.present();
    return instance;
  }

