import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {TouchID} from "@ionic-native/touch-id";
import {StorageManagerProvider} from "../storage-manager/storage-manager";
import {AuthManagerProvider} from "../auth-manager/auth-manager";
import {AlertController} from "ionic-angular";
import {AndroidFingerprintAuth} from "@ionic-native/android-fingerprint-auth";

/*
  Generated class for the TouchManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TouchManagerProvider {

  constructor(private touchID: TouchID, private storage: StorageManagerProvider, private authManager: AuthManagerProvider,
              private alert: AlertController, private androidFingerprintAuth: AndroidFingerprintAuth) {
    console.log('Hello TouchManagerProvider Provider');
  }

  public check(callback) {


    this.storage.contains("touch-info", (contains) => {

      if (contains) {

        this.storage.fetch("touch-info", (savedData) => {

          this.touchID.isAvailable().then(_ => {
            this.touchID.verifyFingerprint("Mit TouchID einloggen").then(_ => {


              this.authManager.login(savedData.name, savedData.password, (success) => {

                callback(success);
              })
            })

          }).catch(_ => {

            this.androidFingerprintAuth.isAvailable().then(result => {
              console.log("Android fingerprint available:", result)
              if (result.isAvailable) {

                this.androidFingerprintAuth.decrypt({token: savedData.token, clientId: 'ClinetApp', dialogTitle: "Fingerabdruck", dialogMessage: "Fingerabdruck zum Anmelden bei WZK Kliniken", username: savedData.name }).then(data => {

                  this.authManager.login(savedData.name, data.password, (success) => {

                      callback(success);
                  })
                }).catch(err => {
                  console.log("Error when trying to call decrypt method", err)
                })

              }
            })
          })

        });
      }
    })


  }

  public disableTouch(callback) {
    this.storage.contains("touch-info", (contains) => {
      if (contains) {
        this.storage.remove("touch-info", () => {
          this.storage.save("touch-ignore", true, () => {
            callback();
          })
        })
      }
      else {
        this.storage.save("touch-ignore", true, () => {
          callback();
        })
      }
    })
  }

  /**
   * Ensures that the next time a manual log-in happens,
   * the user will be given the chance to activate the Touch ID.
   * On success, it calls the callback function.
   * @param callback
   */
  public activateTouch(callback) {
    this.storage.contains("touch-ignore", doesIgnoreTouch => {
      if (doesIgnoreTouch) {
        this.storage.remove("touch-ignore", () => {
          callback();
        })
      }
      else {
        callback();
      }
    })
  }

  public handle(name, password) {
    this.touchID.isAvailable().then(_ => {

      const me = this;
      me.storage.contains("touch-ignore", (does) => {

        if (!does) {
          let confirm = me.alert.create({
            title: 'Touch ID?',
            message: 'Mchten Sie sich mit TouchID anmelden knnen?',
            buttons: [{
              text: 'Nicht aktivieren',
              handler: () => {
                this.storage.save("touch-ignore", true, () => {
                })
              }
            }, {
              text: 'Aktivieren',
              handler: () => {
                this.storage.contains("touch-info", (contains) => {
                  if (contains) {
                    this.storage.remove("touch-info", () => {
                      this.storage.save("touch-info", {name: name, password: password}, () => {
                      });
                    });
                  } else {
                    this.storage.save("touch-info", {name: name, password: password}, () => {
                    });
                  }
                });
              }
            }]
          });
          confirm.present();
        }

      });


    }).catch(_ => {

      this.androidFingerprintAuth.isAvailable().then(result => {

        if (result.isAvailable) {

          this.storage.contains("touch-ignore", (contains) => {

            if (!contains) {

              let confirm = this.alert.create({
                title: 'Fingerabdruck?',
                message: 'Mchten Sie sich mit ihrem Fingerabdruck anmelden knnen?',
                buttons: [{
                  text: 'Nicht aktivieren',
                  handler: () => {
                    this.storage.save("touch-ignore", true, () => {
                    })
                  }
                }, {
                  text: 'Aktivieren',
                  handler: () => {
                    const androidOpts = {
                      clientId: 'ClinetApp',
                      username: name,
                      password: password};
                    this.storage.contains("touch-info", (contains) => {
                      if (contains) {
                        this.storage.remove("touch-info", () => {
                          this.androidFingerprintAuth.encrypt(androidOpts).then(result => {
                            if (result.withFingerprint) {
                              this.storage.save("touch-info", {token: result.token, name: name}, () => {
                              });
                            }
                          });
                        });
                      } else {
                        this.androidFingerprintAuth.encrypt(androidOpts).then(result => {
                          if (result.withFingerprint) {
                            this.storage.save("touch-info", {token: result.token, name: name}, () => {
                            });
                          }
                        });
                      }
                    });
                  }
                }]
              });
              confirm.present();
            }
          })
        }
      }).catch(exc => {
        console.log("This device doesn't have a fingerprint sensor.")
      })
    })
  }
}



