import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import { Storage } from "@ionic/storage";

/*
  Generated class for the StorageManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StorageManagerProvider {
  constructor(public storage: Storage) {
    console.log("Hello StorageManagerProvider Provider");
  }
  public save(key: string, value: any, callback) {
    this.storage.set(key, value).then((_) => {
      callback();
    });
  }

  public contains(key, callback) {
    this.storage.get(key).then((val) => {
      callback(val != null && val != undefined);
    });
  }

  public foreachCall(callbackForEach) {
    const me = this;
    this.storage.keys().then((keys) => {
      for (let i = 0; i < keys.length; i++) {
        me.fetch(keys[i], callbackForEach);
      }
    });
  }
  public fetch(key, callback) {
    this.storage.get(key).then((val) => {
      callback(val);
    });
  }
  public remove(key, callback) {
    this.storage.remove(key).then((_) => {
      callback();
    });
  }
  public clear(callback) {
    this.storage.clear().then((_) => {
      callback();
    });
  }
  public myclear() {
    this.storage.clear();
  }
  public removeIfContains(key, callback = () => {}) {
    this.contains(key, (ckdContains) => {
      if (ckdContains) this.remove(key, () => callback());
      else callback();
    });
  }
}



