import { Injectable }                                       from '@angular/core';
import {Http, Headers, RequestOptions, ResponseContentType} from "@angular/http";
import 'rxjs/add/operator/map';
import {Info}                                               from "../../services/statics";
import {StorageManagerProvider}                             from "../storage-manager/storage-manager";
import {isArray}                                            from "rxjs/util/isArray";
import {isObject}                                           from "rxjs/util/isObject";

/*
  Generated class for the ApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ApiProvider {

  public target = Info.apiURL;
  public targetBackend = Info.backendUrl();
  public CSRFToken = '';

  constructor(public http: Http, private storage: StorageManagerProvider) {
  }

  /**
   * Makes a post request to the API endpoint.
   * @param path The relative path to the base API endpoint
   * @param data
   * @param headers
   * @param callback
   * @param key
   * @param json
   * @param responseType
   */
  public post(path:string, data:any, headers:any[], callback, key:string = null,  json = false, responseType = ResponseContentType.Text) {
    const opts = new Headers();
    opts.append("Content-type", "application/json");
    for(let i = 0; i < headers.length; i++) {
      opts.append(headers[i].name, headers[i].value);
    }
    if(key !== null && key.length > 0) data.key = key;
    let headersObject = new RequestOptions({headers: opts, responseType: responseType});
    if(!json) {
      this.http.post(this.target + path, data, headersObject).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    } else {
      this.http.post(this.target + path, data, headersObject).map(res => res.json()).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    }
  }

  public postBackend(path:string, data:any, headers:any[], callback, key:string = null,  json = false, responseType = ResponseContentType.Text) {
    if (this.CSRFToken == '') {
      this.getBackend('/rest/session/token', [], (success, data) => {
        this.CSRFToken = data.text()
      });
    }
    const opts = new Headers();
    opts.append("Content-type", "application/json");
    if (this.CSRFToken != '') {
      opts.append("X-CSRF-Token", this.CSRFToken);
    }
//    opts.append("withCredentials", "true");
    console.info('this.CSRFToken is ', this.CSRFToken);
    for(let i = 0; i < headers.length; i++) {
      opts.append(headers[i].name, headers[i].value);
    }
    if(key !== null && key.length > 0) data.key = key;
    let headersObject = new RequestOptions({headers: opts, responseType: responseType});
    if(!json) {
      this.http.post(this.targetBackend + path, data, headersObject).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    } else {
      this.http.post(this.targetBackend + path, data, headersObject).map(res => res.json()).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    }
  }

  /**
   * Make post request to Drupal backend.
   *
   * @param uri
   * @param payload
   * @param headers
   * @param callback
   */
  public backendPost(uri: string, payload: any = [], headers: any[] = [], callback = null) {
    console.info('backendPost ---------------------------------');
    this.storage.fetch('token_info', (token_info) => {
      let opts = this.prepareHeaders(token_info, headers);
      // const opts = new Headers();
      // opts.append("Content-type", "application/json");
      //
      // for(let i = 0; i < headers.length; i++) {
      //   opts.append(headers[i].name, headers[i].value);
      // }
      //
      // if (isObject(token_info)) {
      //   opts.append("Authorization", token_info.token_type + ' ' + token_info.access_token);
      // }

      console.info(opts);
      let headersObject = new RequestOptions({headers: opts});

      this.http.post(this.targetBackend + uri, JSON.stringify(payload), headersObject).subscribe(
          Response => {
            console.info(Response);
            console.info(Response.status);
            callback(true, Response.json());
          },
          Error => {
            if (Error.status == 401) {
              this.postBackendRefreshOAuthToken(token_info.refresh_token, (success, new_token) => {
                if (success) {
                  this.storage.save('token_info', new_token, _ => {
                    let opts = this.prepareHeaders(new_token, headers);
                    let headersObject = new RequestOptions({headers: opts});

                    this.http.post(this.targetBackend + uri, payload, headersObject).subscribe(
                        Response => {
                          callback(true, Response.json());
                        },
                        Error => {
                          callback(false, Error);
                        }
                    );
                  })
                }
              });
            }
            else if (Error.status == 403) {
              callback(false, null);
            }
          })
    });
//    this.http.post(this.targetBackend).subscribe().
  }

  /**
   * Create headers list for request.
   *
   * @param token_info
   * @param headers
   * @private
   */
  private prepareHeaders(token_info: any, headers: any[] = []) {
    const opts = new Headers();
    opts.append("Content-type", "application/json");

    for(let i = 0; i < headers.length; i++) {
      opts.append(headers[i].name, headers[i].value);
    }

    if (isObject(token_info)) {
      opts.append("Authorization", token_info.token_type + ' ' + token_info.access_token);
    }

    return opts;
  }

  public postBackendGetOAuthToken(user, pass, callback) {
    const formData = new FormData();
    formData.append("grant_type", "password");
    formData.append("client_id", Info.backendOauthClientId);
    formData.append("client_secret", Info.backendOauthClientPassword);
    formData.append("username", user);
    formData.append("password", pass);
    formData.append("scope", Info.backendOauthClientScope);

    const opts = new Headers();
//    opts.append("withCredentials", "true");
    let headersObject = new RequestOptions({headers: opts});

    this.http.post(this.targetBackend + "/oauth/token", formData, headersObject).map(res => res.json()).subscribe(val => {
      callback(true, val);
    }, error => {
      callback(false, error);
    })
  }

  public postBackendRefreshOAuthToken(refresh_token, callback) {
    const formData = new FormData();
    formData.append("grant_type", "refresh_token");
    formData.append("refresh_token", refresh_token);
    formData.append("client_id", Info.backendOauthClientId);
    formData.append("client_secret", Info.backendOauthClientPassword);

    const opts = new Headers();
//    opts.append("withCredentials", "true");
    let headersObject = new RequestOptions({headers: opts});

    this.http.post(this.targetBackend + "/oauth/token", formData, headersObject).map(res => res.json()).subscribe(val => {
      callback(true, val);
    }, error => {
      callback(false, error);
    })
  }

  /**
   * Makes a get request to the API endpoint.
   * @param path The relative path to the base API endpoint
   * @param headers
   * @param callback
   * @param json
   * @param responseType
   */
  public get(path:string,  headers:any[], callback, json = false, responseType = ResponseContentType.Text) {
    const opts = new Headers();
    if(json) opts.append("Content-type", "application/json");
    for(let i = 0; i < headers.length; i++) {
      opts.append(headers[i].name, headers[i].value);
    }

    let headersObject = new RequestOptions({headers: opts, responseType: responseType});
    if(!json) {
      this.http.get(this.target + path, headersObject).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    } else {
      this.http.get(this.target + path, headersObject).map(res => res.json()).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    }
  }

  public getBackend(path:string,  headers:any[], callback, json = false, responseType = ResponseContentType.Text) {
    const opts = new Headers();
    if (json) opts.append("Content-type", "application/json");

    for(let i = 0; i < headers.length; i++) {
      opts.append(headers[i].name, headers[i].value);
    }
//    opts.append("withCredentials", "true");
    opts.append("Accept", "application/vnd.api+json");

    let headersObject = new RequestOptions({headers: opts, responseType: responseType});
    if(!json) {
      this.http.get(this.targetBackend + path, headersObject).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    } else {
      this.http.get(this.targetBackend + path, headersObject).map(res => res.json()).subscribe(val => {
        callback(true, val);
      }, error => {
        callback(false, error);
      })
    }
  }

}



