import { Injectable } from '@angular/core';
import {AuthManagerProvider} from "../auth-manager/auth-manager";
import {PlatformManagerProvider} from "../platform-manager/platform-manager";
import {GuiManagerProvider} from "../gui-manager/gui-manager";

@Injectable()
export class ActivityTrackerProvider {

  constructor(private authManager: AuthManagerProvider, private platform: PlatformManagerProvider, private guiManagerProvider: GuiManagerProvider) {
    this.userActionDetected();
    let me = this;
    setInterval(function() {
      let expirationDateString = me.getCookie("expires");
      let expirationDate = new Date(expirationDateString);
      if (new Date() > expirationDate && authManager.isLoggedIn == true && platform.isWebApplication()) {
        authManager.logout();
        me.guiManagerProvider.showLoggedOutInactivity();
      }
    }, 5000);
  }

  /**
   * Should be called each time that the user does an action.
   */
  userActionDetected() {
    let d = new Date();
    let minutes = 20;
    d.setTime(d.getTime() + (minutes*60*1000));
    let expires = "expires="+ d.toUTCString();
    document.cookie = expires +";";
  }

  /**
   * Retrieves the cookie with the name specified.
   * @param cname
   */
  getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(';');
    for(let i = 0; i <ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

}



