import { Component, ViewChild } from "@angular/core";
import { Content, ModalController, Nav, Platform } from "ionic-angular";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { HomePage } from "../pages/home/home";
import { GuiManagerProvider } from "../providers/gui-manager/gui-manager";
import { AuthManagerProvider } from "../providers/auth-manager/auth-manager";
import { IntroSliderPage } from "../pages/intro-slider/intro-slider";
import { LoginPage } from "../pages/login/login";
import { CheckinServiceProvider } from "../providers/checkin-service/checkin-service";
import { ActivityTrackerProvider } from "../providers/activity-tracker/activity-tracker";
import { PlatformManagerProvider } from "../providers/platform-manager/platform-manager";

declare var cordova: any;

@Component({
  templateUrl: "app.html",
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  @ViewChild(Content) content: Content;
  private homePage = { title: "Home", component: HomePage };

  rootPage: any = "LoginPage";
  appManager: any = {};

  // Importing from Visit iran
  public profile_pic: string = "assets/sidebar/no_pic.jpg";
  public fullname: string = "unknown name";
  public acs_lvl_typ: string = "";

  goEditProfile() {
    //this.nav.push("ProfilePage");
    console.log("Unsupported function: goEditprofile (app.component.ts)");
  }
  ///

  get showMsgCenter() {
    return this.guiManager.messageCenter;
  }

  get pages() {
    this.content.resize();
    return this.guiManager.pages;
  }

  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public guiManager: GuiManagerProvider,
    private authProvider: AuthManagerProvider,
    private modalCtrl: ModalController,
    private checkinProvider: CheckinServiceProvider,
    private tracker: ActivityTrackerProvider,
    private platformManager: PlatformManagerProvider
  ) {
    this.initializeApp();
    this.detectOldIE();
  }

  initializeApp() {
    this.tracker.userActionDetected();
    const me = this;
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();

      if (this.platformManager.isWebApplication()) {
        this.nav._app.viewDidEnter.subscribe((view, error, complete) => {
          this.tracker.userActionDetected();
        });
      }
      this.authProvider.checkLogin((success) => {
        if (success) this.nav.setRoot(HomePage);
      });
    });
  }

  private detectOldIE() {
    var isOldIE = navigator.userAgent.match(/Trident/);
    console.log("Monkey patched because of old IE" + isOldIE);

    if (isOldIE) {
      document.body.classList.add("ie-old");
      this.appManager.isOldIEVersion = true;
      console.log("Monkey patched because of old IE");
      this.checkinProvider.isIE = true;
    }
  }

  openMessageCenter() {
    const modal = this.modalCtrl.create("MessageCenterPage");
    modal.present();
  }

  openDrawer() {
    this.nav.push("DrawPage");
  }

  logout() {
    this.authProvider.logout();
    this.nav.setRoot(HomePage);
  }

  get loggedIn() {
    return this.authProvider.isLoggedIn;
  }

  sitesFromKey(key) {
    return this.pages[key];
  }

  get pageKeys() {
    return Object.keys(this.pages);
  }

  get footerNeed() {
    return !this.authProvider.isLoggedIn && !this.guiManager.isLoginPageActive;
  }

  goToLoginPage() {
    this.nav.setRoot("LoginPage");
  }

  checkDisabled(item) {
    if (item.loader == null) return false;

    return this.guiManager.loaders[item.loader] == true;
  }

  openHomePage() {
    this.nav.setRoot(HomePage, {}, { animate: true, direction: "forward" });
  }

  openPage(page) {
    this.nav.setRoot(page.component, page.params != null ? page.params : {}, {
      animate: true,
      direction: "forward",
    });
  }
}



