import {Node} from "../../providers/checkin-service/checkin.modals";

export class Process {
    static rulesFullFilled(condition: string, item: Node, currentValues) {
//         if (item.id.endsWith('wenn_ja_wie_lang_in_wochen_')) {
// //          console.info(item.rules);
//         }
        for (let i = 0; i < item.rules.length; i++) {
            const current = item.rules[i];

            if (current.effect != condition) continue;

            const conditions = current.conditions;
            for (let c = 0; c < conditions.length; c++) {
                const currentCondition = conditions[c];
                const nameSearched = currentCondition.name;
                const type = currentCondition.type;
                const value = currentCondition.value;

                // if (item.id.endsWith('wenn_ja_wie_lang_in_wochen_')) {
                //     // console.info(currentCondition);
                //     // console.info(currentValues);
                // }
                let actualValue = null;
                for (let w = 0; w < currentValues.length; w++) {
                    if (currentValues[w].key == nameSearched) {
                        if (currentCondition.fromArray) {
                            actualValue = currentValues[w].value[currentCondition.arrId - 1].ref
                        } else {
                            actualValue = currentValues[w].value;
                        }
                        break;
                    }
                }

                if (actualValue == null) {
                    switch (condition) {
                        case "visible" : {
                            return conditions.length == 0
                        }
                        case "disabled" : {
                            return false
                        }
                    }
                }


                switch (condition) {
                    case "visible" : {
                        return Process.parseVisible(type, actualValue, value)
                    }
                    case "disabled" : {
                        return Process.parseDisabled(type, actualValue, value)
                    }
                }
            }

        }

        switch (condition) {
            case "visible" : {
                return true
            }
            case "disabled" : {
                return false
            }
            case "required" : {
                return Process.parseRequired(item)
            }
        }
    }

    /*
      Returns if the specified Node item or its children have any unfulfilled required Rule.
      Returns true if any required rule is unfulfilled, false otherwise.
     */
    public static parseRequired(item: Node) {
        if (item.children.length == 0) {
            for (let rule of item.rules) {
                if (rule.effect == "required") {
                    return !(item.reflector != null && item.reflector != "");
                }
            }
            return false
        } else {
            let required = false;
            for (let rule of item.rules) {
                if (rule.effect == "required") {
                    required = !(item.reflector != null && item.reflector != "");
                }
            }
            for (let childNode of item.children) {
                if (required) break;
                required = Process.parseRequired(childNode)
            }
            return required;
        }
    }

    private static parseVisible(type: string, actualValue, value) {

        switch (type) {
            case "unchecked" : {
                if (actualValue == true) return false;
                break
            }
            case "filled" : {
                if (value) {
                    if (actualValue.length == 0 || actualValue == false) return false;
                } else {
                    if (actualValue.length != 0 || actualValue == true) return false;
                }
                break
            }
            case "empty" : {
                if (value && actualValue.length != 0) return false;
                if (!value && actualValue.length == 0) return false;
                break;
            }
            case "value" : {
                if (value != actualValue) return false;
                break
            }
            case "!value" : {
                if (value == actualValue) return false;
                break
            }
        }
        return true
    }

    private static parseDisabled(type: string, actualValue, value) {

        switch (type) {
            case "unchecked" : {
                if (actualValue == true) return true;
                break
            }
            case "filled" : {
                if (value) {
                    if (actualValue.length == 0) return true;
                } else {
                    if (actualValue.length != 0) return true;
                }
                break
            }
            case "empty" : {
                if (value && actualValue.length == 0) return true;
                if (!value && actualValue.length == 0) return true;
                break;
            }
            case "value" : {
                if (value != actualValue) return true;
                break
            }
            case "!value" : {
                if (value == actualValue) return true;
                break
            }
        }
        return false
    }
}



