import {Component} from '@angular/core';
import {Platform, NavParams, ViewController} from 'ionic-angular';
import {LinkManagerProvider} from "../../providers/link-manager/link-manager";
import {Http} from "@angular/http";


@Component({
  templateUrl: 'modal-box.html'

})
export class ModalContentPage {
  public title: any;
  public calorie: any;
  public additives: any;
  public value: any;
  public menu: any;
  public menuData: any;
  public nids: any = [];
  public time: any;

  constructor(public platform: Platform,
              public params: NavParams,
              public viewCtrl: ViewController,
              private http: Http,
              private linkManager: LinkManagerProvider) {
    this.title = this.params.get('art');

    this.nids = this.params.get('food_ids');
    this.time = this.params.get('time');

    console.log("Showing the day's nids:", this.nids);
    const me = this;

    http.get( this.linkManager.getMenuFoodAdditionals(this.nids.join('+')) ).map(res => res.json()).subscribe(response => {
/*
      let reference_array = me.nids;
      let reference_object = {};
      for (let i = 0; i < reference_array.length; i++) {
        reference_object[reference_array[i]] = i;
      }
      response.sort(function(a, b) {
        return reference_object[a.nid] - reference_object[b.nid];
      });
*/
      response.sort(function(a, b) {
        return me.nids.indexOf(a.nid) - me.nids.indexOf(b.nid);
      });
      console.log(response);
      this.menuData = response;
    });
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



