export class CalendarEvent {

  public static CUSTOM_TEXT_FOR_APPLICATION: string = "--- Created by CLINET application.";
  public static CLINET_CALENDAR_TYPE: string = "CLINET";
  public static GLOBAL_CALENDAR_TYPE: string = "Global";
  public static EXTERNAL_CALENDAR_TYPE: string = "External";
  public static TYPE1_EVENT_TYPE: number = 1;
  public static TYPE2_EVENT_TYPE: number = 2;
  public static GLOBAL_EVENT_TYPE: number = 3;
  private static TYPE1_EVENT_TYPE_TEXT: string = "Type1 event";
  private static TYPE2_EVENT_TYPE_TEXT: string = "Type2 event";
  private static GLOBAL_EVENT_TYPE_TEXT: string = "Global event";
  public static ALL_EVENT_TYPES: number[] = [CalendarEvent.TYPE1_EVENT_TYPE, CalendarEvent.TYPE2_EVENT_TYPE, CalendarEvent.GLOBAL_EVENT_TYPE];

  title: string;
  location: string;
  notes: string;
  start: Date;
  startTime: Date;
  end: Date;
  endTime: Date;
  allday: boolean;
  allDay: boolean;
  message: string;
  id: number;
  calendarType: string;
  typeOfMeeting: number;
  attending: boolean;
  participants: string;
  practitioner: string;

    constructor(start = new Date(), end = new Date(), title = "", location = "", notes = "", allday = false, message = "", calendarType = CalendarEvent.CLINET_CALENDAR_TYPE, id = -1, typeOfMeeting = CalendarEvent.TYPE1_EVENT_TYPE, attending = false, participants = "", practitioner = "") {
        this.title = title;
        this.location = location;
        this.notes = notes;
        this.startTime = start;
        this.endTime = end;
        this.allday = allday;
        this.message = message;
        this.id = id;
        this.calendarType = calendarType;
        this.typeOfMeeting = typeOfMeeting;
        this.attending = attending;
        this.participants = participants;
        this.practitioner = practitioner;
    }

  public static getEventTypeByNumber(type: number) {
      if (type == CalendarEvent.TYPE1_EVENT_TYPE)
          return CalendarEvent.TYPE1_EVENT_TYPE_TEXT;
      if (type == CalendarEvent.TYPE2_EVENT_TYPE)
          return CalendarEvent.TYPE2_EVENT_TYPE_TEXT;
      if (type == CalendarEvent.GLOBAL_EVENT_TYPE)
          return CalendarEvent.GLOBAL_EVENT_TYPE_TEXT;
  }

  public getHTMLClassName() {
      //console.log("Cal event, HTML class: ", event);
      if (this.typeOfMeeting == CalendarEvent.TYPE2_EVENT_TYPE) {
          return "CustomColorType2";
      }
      else {
          return "CustomColorUnknownType";
      }
  }
}



