import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/add/operator/map';

/*
  Generated class for the DebuggerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DebuggerProvider {

  private printStack = [];
  public ownCallback = (content) => {

  };

  set callback(item) {
    this.ownCallback = item;
  }
  constructor(public http: Http) {
    console.log('Hello DebuggerProvider Provider');
  }

  get stack() {
    return this.printStack;
  }

  register() {

    const me = this;
    window["logCallback"] = (args) => {

      var content = "LOG: ";
      const obType = typeof {};
      const arrType = typeof [];
      for (let i = 0; i < args.length; i++) {
        const current = args[i];

        if (typeof current == obType || typeof current == arrType) {
          content += JSON.stringify(current) + " ";
        }
      }

      me.printStack.push(content);
      me.ownCallback(content);
    };
    window["errCallback"] = (args) => {

      var content = "ERR: ";
      const obType = typeof {};
      const arrType = typeof [];
      for (let i = 0; i < args.length; i++) {
        const current = args[i];

        if (typeof current == obType || typeof current == arrType) {
          content += JSON.stringify(current) + " ";
        }
      }

      me.printStack.push(content);
      me.ownCallback(content);
    };
    window["warnCallback"] = (args) => {

      var content = "WARN: ";
      const obType = typeof {};
      const arrType = typeof [];
      for (let i = 0; i < args.length; i++) {
        const current = args[i];

        if (typeof current == obType || typeof current == arrType) {
          content += JSON.stringify(current) + " ";
        }
      }

      me.printStack.push(content);
      me.ownCallback(content);
    };


  }
}



