import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/add/operator/map';

/*
  Generated class for the DrawProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DrawProvider {
    public imageData = {};
    public processed = {};

    constructor(public http: Http) {
        console.log('Hello DrawProvider Provider');
    }

    processImage(path, orig, parent, callback) {
        const backgroundImage = new Image();

        backgroundImage.src = path;
        backgroundImage.onload = () => {
            const canvas = this.createContext(backgroundImage.width, backgroundImage.height);
            canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.height);
            console.log(path)
            canvas.getContext('2d').drawImage(backgroundImage, 0, 0, canvas.width, canvas.height);

            const img = new Image();
            img.src = this.imageData[parent + path];
            img.onload = () => {
                canvas.getContext('2d').drawImage(img, 0, 0, canvas.width, canvas.height);
                const data = canvas.toDataURL("image/png");
                this.processed[parent+path] = data;
                callback(data);
            }
        };
    }

    private createContext(width, height) {
        var canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;

        return canvas;
    }

}



