export class UserCalendar {
  name: string;
  displayname: string;
  id: number;
  sync: boolean;

  constructor(name?:string, displayname?:string, id?:number, sync = false) {
    this.name = name;
    this.displayname = displayname;
    this.id = id;
    this.sync = sync;
  }
}



