import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/add/operator/map';
import {ModalController} from "ionic-angular";

/*
  Generated class for the ElectronProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ElectronProvider {

  public isElectron;
  private websocket;
  private currentCallback = (data) => {

  };

  constructor(public http: Http, private  modalCtrl: ModalController) {

    const me = this;
    try {

      const socket = new WebSocket("ws://127.0.0.1:56796");

      socket.onclose = function (evt) {
        if (evt.code == 3001) {
        } else {
          me.isElectron = false;
        }
      };

      socket.onmessage = (rep) => {
        const data = rep.data;

        if (data.action == "reset") {
          this.modalCtrl.create('PasswordResetPage', {key: data.key})
        } else {
          me.currentCallback(JSON.parse(data));
        }
      };
      socket.onopen = () => {
        me.isElectron = true;
        me.websocket = socket;
        socket.send(JSON.stringify({action: "handshake"}));
      };
    } catch (error) {
      console.log(error)
    }
  }

  downloadFile(name, data, callback) {

    this.currentCallback = callback;

    this.websocket.send(JSON.stringify({
      action: "download",
      data: {point: "api.clinetplatforms.com", data: data, port: 8243, name: name}
    }))


  }

}



