import { BrowserModule } from "@angular/platform-browser";
import { ErrorHandler, NgModule } from "@angular/core";
import { IonicApp, IonicErrorHandler, IonicModule } from "ionic-angular";
import { SplashScreen } from "@ionic-native/splash-screen";
import { StatusBar } from "@ionic-native/status-bar";
import { MyApp } from "./app.component";
import { HomePage } from "../pages/home/home";
import { AuthManagerProvider } from "../providers/auth-manager/auth-manager";
import { GuiManagerProvider } from "../providers/gui-manager/gui-manager";
import { StorageManagerProvider } from "../providers/storage-manager/storage-manager";
import { IonicStorageModule } from "@ionic/storage";
import { CheckinServiceProvider } from "../providers/checkin-service/checkin-service";
import { ApiProvider } from "../providers/api/api";
import { TouchManagerProvider } from "../providers/touch-manager/touch-manager";
import { TouchID } from "@ionic-native/touch-id";
import { AndroidFingerprintAuth } from "@ionic-native/android-fingerprint-auth";
import { OtpProvider } from "../providers/otp/otp";
import { File } from "@ionic-native/file";
import { FileManagerProvider } from "../providers/file-manager/file-manager";
import { ElectronProvider } from "../providers/electron/electron";
import { FileOpener } from "@ionic-native/file-opener";
import { ModalContentPage } from "../pages/food-card/modal-box.page";
import { NgCalendarModule } from "ionic2-calendar";
import { Geolocation } from "@ionic-native/geolocation";
import { InAppBrowser } from "@ionic-native/in-app-browser";
import { DrawProvider } from "../providers/draw/draw";
import { MessagesProvider } from "../providers/messages/messages";
import { DebuggerProvider } from "../providers/debugger/debugger";
import { Calendar } from "@ionic-native/calendar";
import { CalendarProvider } from "../providers/calendar/calendarProvider";
import { FileChooser } from "@ionic-native/file-chooser";
import { FilePath } from "@ionic-native/file-path";
import { IOSFilePicker } from "@ionic-native/file-picker";
import { BackgroundFetch } from "@ionic-native/background-fetch";
import { PhonegapLocalNotification } from "@ionic-native/phonegap-local-notification";
import { Firebase } from "@ionic-native/firebase";
import { PlatformManagerProvider } from "../providers/platform-manager/platform-manager";
import { LinkManagerProvider } from "../providers/link-manager/link-manager";
import { ActivityTrackerProvider } from "../providers/activity-tracker/activity-tracker";
import { FormManagerProvider } from "../providers/form-manager/form-manager";
import { AngularFireModule } from "@angular/fire";
import { AngularFireAuthModule } from "@angular/fire/auth";
import { environment } from "../environments/environment";
import { HttpModule } from "@angular/http";
import { SimpleTextModalPage } from "../pages/simple-text-modal/simple-text-modal";
import { NutzungsbedingungenModalPage } from "../pages/Nutzungsbedingungen/nutzungsbedingungen-modal";
import { DatenschutzerklaerungModalPage } from "../pages/Datenschutzerklarung/datenschutzerklaerung-modal";
import { helpModalPage } from "../pages/help/help-modal";


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ModalContentPage,
    SimpleTextModalPage,
    NutzungsbedingungenModalPage,
    DatenschutzerklaerungModalPage,
    helpModalPage,
  ],
  imports: [
    BrowserModule,
    NgCalendarModule,

    IonicModule.forRoot(MyApp, {
      backButtonText: "Zurck",
      scrollAssist: false,
      autoFocusAssist: false,
    }),
    IonicStorageModule.forRoot({
      name: "__mydb",
      driverOrder: ["sqlite", "websql", "indexeddb", "localstorage"],
    }),
    HttpModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireAuthModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ModalContentPage,
    SimpleTextModalPage,
    NutzungsbedingungenModalPage,
    DatenschutzerklaerungModalPage,
    helpModalPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },

    AuthManagerProvider,
    GuiManagerProvider,
    StorageManagerProvider,
    CheckinServiceProvider,
    ApiProvider,
    TouchManagerProvider,
    TouchID,
    AndroidFingerprintAuth,
    OtpProvider,
    File,
    FileManagerProvider,
    ElectronProvider,
    FileOpener,
    Geolocation,
    InAppBrowser,
    DrawProvider,
    MessagesProvider,
    DebuggerProvider,
    Calendar,
    CalendarProvider,
    FileChooser,
    IOSFilePicker,
    FilePath,
    BackgroundFetch,
    PhonegapLocalNotification,
    Firebase,
    PlatformManagerProvider,
    LinkManagerProvider,
    ActivityTrackerProvider,
    FormManagerProvider,
  ],
})
export class AppModule {}



