import { Injectable } from "@angular/core";
import { Info } from "../../services/statics";

/*
  Generated class for the LinkManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LinkManagerProvider {
  constructor() {}

  // If you create new webform in Drupal, you can retrieve the elements and structure of the webform with this url:
  // (BACKEND_URL)/webform_rest/elements/WEBFORM_NAME?_format=hal_json
  private webforms = {
    //    "": Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    "": "/rest/app/checkin/template?_format=json",
    saulgau: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    waldsee: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    salzelmen: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    wurzach: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    aplenblick: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    argenta: Info.merge("/webform_rest/checkin_2/elements?_format=hal_json"),
    schwabenland: Info.merge(
      "/webform_rest/checkin_2/elements?_format=hal_json",
    ),
    niederbayern: Info.merge(
      "/webform_rest/checkin_2/elements?_format=hal_json",
    ),
  };
  private foodMenus = {
    "": Info.merge("/api/menu"),
    saulgau: Info.merge("/saulgau/api/menu"),
    waldsee: Info.merge("/waldsee/api/menu"),
    salzelmen: Info.merge("/salzelmen/api/menu"),
    wurzach: Info.merge("/wurzach/api/menu"),
    aplenblick: Info.merge("/aplenblick/api/menu"),
    argenta: Info.merge("/argenta/api/menu"),
    schwabenland: Info.merge("/schwabenland/api/menu"),
    niederbayern: Info.merge("/niederbayern/api/menu"),
  };
  private infoIdsLists = {
    "": Info.merge("/api/infos/ids/114"),
    saulgau: Info.merge("/saulgau/api/infos/ids"),
    waldsee: Info.merge("/waldsee/api/infos/ids"),
    salzelmen: Info.merge("/salzelmen/api/infos/ids"),
    wurzach: Info.merge("/wurzach/api/infos/ids"),
    aplenblick: Info.merge("/aplenblick/api/infos/ids"),
    argenta: Info.merge("/argenta/api/infos/ids"),
    schwabenland: Info.merge("/schwabenland/api/infos/ids"),
    niederbayern: Info.merge("/niederbayern/api/infos/ids"),
    "111": Info.merge("/api/infos/ids/111", false),
    "114": Info.merge("/api/infos/ids/114", false),
  };

  private newsList = {
    "": "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=8&linkprefix=https://www.rehabilitationsklinik-bad-saulgau.de/154.html?id=",
    saulgau:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=8&linkprefix=https://www.rehabilitationsklinik-bad-saulgau.de/154.html?id=",
    waldsee:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=7&linkprefix=https://www.klinik-im-hofgarten.de/154.html?id=",
    salzelmen:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=11&linkprefix=https://www.rehaklinik-bad-salzelmen.de/154.html?id=",
    wurzach:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=6&linkprefix=https://www.rehabilitationsklinik-bad-wurzach.de/154.html?id=",
    aplenblick:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=3&linkprefix=https://www.klinik-alpenblick.de/154.html?id=",
    argenta:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=2&linkprefix=https://www.argentalklinik.de/154.html?id=",
    schwabenland:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=1&linkprefix=https://www.klinik-schwabenland.de/154.html?id=",
    niederbayern:
      "https://www.wz-kliniken.de/news-rss.php?section=2&kliniken=10&linkprefix=https://www.klinik-niederbayern.de/154.html?id=",
  };

  public getNews(clinic: string = ""): string {
    console.log("getNews is: " + this.newsList[clinic]);
    return this.newsList[clinic] == undefined
      ? this.newsList[""]
      : this.newsList[clinic];
  }

  public getRegistrierungsanweisungenAsJSON(): string {
    return Info.merge("/registrierungsanweisungen?_format=json", false);
  }

  public getNutungsbedingungenAsHalJSON(): string {
    console.log(Info.merge("/nutzungsbedingungen?_format=hal_json", false));

    return Info.merge("/nutzungsbedingungen?_format=hal_json", false);
  }

  public getDatenschutzAsJSON(): string {
    return Info.merge("/datenschutz?_format=json", false);
  }

  public getWebformAsHalJSON(clinic: string = ""): string {
    console.log("getWebformAsHalJSON is: " + this.webforms[clinic]);
    return this.webforms[clinic] == undefined
      ? this.webforms[""]
      : this.webforms[clinic];
  }

  public getInfoIdsList(clinic: string = ""): string {
    console.log("getInfoIdsList is: " + this.infoIdsLists[clinic]);
    return this.infoIdsLists[clinic] == undefined
      ? this.infoIdsLists[""]
      : this.infoIdsLists[clinic];
  }

  public getMenu(clinic: string = ""): string {
    console.log("getMenu is: " + this.foodMenus[clinic]);
    return this.foodMenus[clinic] == undefined
      ? this.foodMenus[""]
      : this.foodMenus[clinic];
  }
  /**
   * @param subFoodsIds String of IDs combined with the plus symbol
   */
  public getMenuFoodAdditionals(subFoodsIds: string): string {
    return Info.merge("/menu/food/additionals" + "/" + subFoodsIds, false);
  }

  /**
   *
   * @param ids String of IDs combined with the plus symbol
   */
  public getInfos(ids: string): string {
    return Info.merge("/api/infos" + ids, false);
  }
}



