import { Injectable }           from '@angular/core';
import {Http}                   from "@angular/http";
import {Node}                   from "../checkin-service/checkin.modals";
import {WebForm}                from "./form-modal";
import {StorageManagerProvider} from "../storage-manager/storage-manager";
import {ApiProvider}            from "../api/api";

/*
  Generated class for the FormManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FormManagerProvider {

  private formsStructure = {};

  constructor(public http: ApiProvider, private storageProvider: StorageManagerProvider) {
    console.log('Hello FormManagerProvider Provider');
  }

  public retrieveWebFormStructureFrom(url: string, accountId: string, callback: (form: WebForm) => void) {
    console.info('URL is: ' + url);
    if (url != null) {
      const getForm = (content) => {
        let form = new WebForm(accountId, WebForm.getFormId(url) ,this.storageProvider);
         form.parse(content, (formCallback) => {
           //TODO maybe do something extra here, although doesn't seem that is needed
           callback(formCallback);
         })
      };

      if (this.formsStructure.hasOwnProperty(url)) {
        console.log("Content already in the memory", this.formsStructure[url]);
        getForm( this.formsStructure[url] );
      }
      else {
        this.storageProvider.fetch('token_info', (token_info) => {
          this.http.getBackend(url,
                               [{name: 'Authorization', value: token_info.token_type + " " + token_info.access_token}],
                               (success, data) =>  {
                                    console.log("Content received by the backend: ", data);
                                    this.formsStructure[url] = data;
                                    getForm(this.formsStructure[url]);
                               }, true);
        });



        // this.http.getBackend(url, [], null).map(res => res.json()).subscribe(content => {
        //   console.log("Content received by the backend: ", content);
        //   this.formsStructure[url] = content;
        //   getForm( this.formsStructure[url] );
        // });
      }

    }
    else {
      console.log("retrieveWebFormStructureFrom: URL is null")
      callback(null)
    }
  }




}



