export class Condition {
  public fromArray = false;
  public arrId = 0;

  constructor(public name: string, public type: string, public value: any) {
  }

}

export class Rule {
  public conditions: Condition[] = [];

  constructor(public effect: string) {
  }

}

export class Node {
  // If you uncomment this and use the variable, it results to circular reference object in the checkin module
  // which causes JSON.stringify to fail, thus resulting in an error when trying to save and restore the checkin data
  //public parent;

  public subGroupCount = 0;
  public subGroups = {};
  public canMultiply = false;
  public selfMultiply = false;
  public children: Node[] = [];
  public fields = [];
  public reflector: any;
  public reflector_other: any;
  public rules: Rule[] = [];
  public visible = true;
  public style = false;

  get isLegitValue() {
    let current = this.reflector;
    let values = this.getField("values");

    return (values.indexOf(current) != -1);
  }

  get isOtherChecked() {
    let output = false;
    let values = this.getField("values");
    let other = this.getField("other_select");
//    console.info('values is', values);
    if (values != null) {
      for (let i = 0; i < values.length; i++) {
        if (values[i].val == other && values[i].ref == true) {
          output = true;
          break;
        }
      }
    }

    return output;
  }

  constructor(public name: string, public title: string, public type: string, public id: string) {
  }

  containsField(key) {
    for (let i = 0; i < this.fields.length; i++) {
      if (this.fields[i].key == key) return true;
    }
    return false;
  }

  addField(key: string, value: any) {
    if (this.containsField(key)) return;
    this.fields.push({key: key, value: value});
  }

  updateValue(key, value) {
    for (let i = 0; i < this.fields.length; i++) {
      if (this.fields[i].key == key) this.fields[i].value = value;
    }
  }

  getFieldKeys() {
    const arr = [];
    for (let i = 0; i < this.fields.length; i++) {
      arr.push(this.fields[i].key);
    }

    return arr;
  }

  getField(key) {
    for (let i = 0; i < this.fields.length; i++) {
      if (this.fields[i].key == key) return this.fields[i].value;
    }
    return null;
  }

  removeField(key) {
    for (let i = 0; i < this.fields.length; i++) {
      if (this.fields[i].key == key) {
        this.fields.splice(this.fields.indexOf(this.fields[i], 1));
        return;
      }
    }
  }

}



