import { Component } from "@angular/core";
import { ViewController } from "ionic-angular";

@Component({
  selector: "help-modal",
  templateUrl: "help-modal.html",
})
export class helpModalPage {
  constructor(public viewCtrl: ViewController) {}

  close() {
    this.viewCtrl.dismiss();
  }
}



