    public registerEventToTheDatabaseAndSync(event: CalendarEvent) {
        const me = this;
        let key = this.authManager.key;
        let start = moment(event.startTime).format("YYYY-MM-DD HH:mm:ss");
        let end = moment(event.endTime).format("YYYY-MM-DD HH:mm:ss");
        let data = CalendarProvider.addParametersIntoJsonObject({'start': start, 'end': end} ,event);
        //console.log("Making the API call to register the event to DB");
        this.makeAPIcall("/meetings/set", (success, data) => {
            me.getResponseFromAPIAfterSavingEvent(success, data, event); }, key, data);
    }

    public retrieveAndUpdateEventsFromTheDatabase() {
        const me = this;
        let key = this.authManager.key;
        let data = {};
        this.makeBackendAPIcall("/rest/app/meetings?_format=json", (success, data) => { me.parseEventsFromAPICall(data.success, data.data); }, key, data);
    }

    public deleteEventFromTheDatabase(event: CalendarEvent) {
        const me = this;
        if ( event.calendarType == CalendarEvent.GLOBAL_CALENDAR_TYPE && this.authManager.isMedical != true) {
            console.log("You are not authorized to delete Global events.");
            return;
        }
        let key = this.authManager.key;
        let data = JSON.stringify({'key': key, 'id' : event.id});
        this.http.post("/meetings/delete",data,[],(success, data) => { me.getResponseFromAPIAfterDeletingEvent(success, data); });
    }

    public updateEventAttendanceInTheDatabase(event: CalendarEvent) {
        const me = this;
        let key = this.authManager.key;
        let start = moment(event.startTime).format("YYYY-MM-DD HH:mm:ss");
        let end = moment(event.endTime).format("YYYY-MM-DD HH:mm:ss");
        let data = CalendarProvider.addParametersIntoJsonObject({'start': start, 'end': end} ,event);
        console.log("Making the API call to update the event on DB");
        this.makeAPIcall("/meetings/update", (success, data) => {
            console.log("In the callback function after doing the call to DB to update attendance of event"); me.getResponseFromAPIAfterUpdatingAttendanceOfEvent(success, data, event); }, key, data);
    }

    public updateUserList() {
        if (this.usersListUpdating == true) {
            return;
        }
        this.usersListUpdating = true;
        let result = [];
        if (this.authManager.isMedical == true) {
            let query = JSON.stringify({"key": this.authManager.key, "data": "*"});
            console.log("Retrieving user data from the API call...");
            this.http.post("/search", query, [], (success, searchData) => {
                console.log("Finished retrieving user data from the API call...");
                if (searchData.hasOwnProperty("_body")) {
                    let json = JSON.parse(searchData["_body"]);
                    if (json["data"]["level"] == "all") {
                        result = json["data"]["results"];
                    }
                }
                this.setUserList(result);
                this.usersListUpdating = false;
            });
        }
        else {
            this.setUserList(result);
            this.usersListUpdating = false;
        }
    }

    public setUserList(array) {
        this.usersList = array;
        this.usersListFinishedUpdate = true;
    }

